.destination {
  height: 100vh;
  width: 100vw;
  background: url("../assets/destination/background-destination-desktop.jpg");
  background-size: cover;
  padding-top: 212px;
  padding-left: 166.5px;
}

.destination .title-page-destination {
  font-family: "Barlow Condensed";
  display: flex;
  font-size: 28px;
  height: 34px;
  letter-spacing: 4.72px;
  font-weight: 400;
}

.destination .title-page-destination .title-page-destination-number {
  margin-right: 28px;
  opacity: 0.25;
  font-weight: bold;
}

.destination .content-destination {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 64px;
}

.destination .content-destination img {
  height: 445px;
}

.destination .content-destination .text-destination {
  width: 472px;
  justify-content: space-around;
}

.destination .content-destination .text-destination .navbar-planet {
  display: flex;
  list-style: none;
  font-family: "Barlow Condensed";
  height: 34px;
  margin-bottom: 37px;
}

.destination .content-destination .text-destination .navbar-planet .item-planet {
  margin-right: 35px;
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 2.7px;
  font-weight: 400;
}

.destination .content-destination .text-destination .navbar-planet .item-planet:hover {
  cursor: pointer;
}

.destination .content-destination .text-destination .name-planet {
  font-size: 100px;
  font-family: Bellefair;
}

.destination .content-destination .text-destination .description-planet {
  color: #d0d6f9;
  font-size: 18px;
  font-family: "Barlow";
  line-height: 32px;
  text-align: left;
  width: 94%;
  margin-bottom: 54px;
}

.destination .content-destination .text-destination .trait {
  width: 95%;
  height: 1px;
  background-color: #383b4b;
  margin-bottom: 28px;
}

.destination .content-destination .text-destination .stat-planet {
  display: flex;
  font-family: "Bellefair";
  text-transform: uppercase;
  font-size: 28px;
}

.destination .content-destination .text-destination .stat-planet .distance {
  display: flex;
  flex-direction: column;
  margin-right: 79px;
}

.destination .content-destination .text-destination .stat-planet .distance .text-distance {
  font-family: "Barlow Condensed";
  margin-bottom: 12px;
  font-size: 14px;
  letter-spacing: 2.36px;
}

.destination .content-destination .text-destination .stat-planet .travel-time {
  display: flex;
  flex-direction: column;
}

.destination .content-destination .text-destination .stat-planet .travel-time .text-travel {
  font-family: "Barlow Condensed";
  margin-bottom: 12px;
  font-size: 14px;
  letter-spacing: 2.36px;
}

.destination .active {
  border-bottom: white solid 4px;
}
